:root {
    --text-color: #f5f5f5;
}

.characters {
    display: grid;
    padding: 0 7%;
    gap: 1% 4%;
    grid-template-columns: 1fr 1fr;
}

.card {
    display: flex;
    align-items: center;
    border-radius: 8px;
    height: 250px;
    background-color: #3b3d43;
}

.card .image {
    object-fit: cover;
    border-radius: 8px 0 0 8px;
    height: 100%;
}

.card .details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 15px;
}

.card .details .status {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
}

.card .details .status span {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background: green;
}

.card .details .status p {
    font-size: clamp(14px, 0.89vw, 17px);
    font-weight: 500;
    color: var(--text-color);
}

.card .details .location {
    margin-bottom: 10px;
}

.card .details h2 {
    color: var(--text-color);
    font-weight: 700;
    font-size: clamp(18px, 1.46vw, 28px);
}

.card .details p {
    color: gray;
    margin-bottom: 0;
    font-size: 15px;
    font-weight: 600;
}

.card .details span {
    color: var(--text-color);
    font-size: clamp(14px, 0.89vw, 17px)
}

.filter {
    display: flex;
    justify-content: center;
    margin: 40px 0;
}

.filter input {
    border-radius: 8px;
    height: 40px;
    border-color: transparent;
    width: 25%;
    outline: none;
    caret-color: #495057;
    padding-left: 15px;
    font-size: 15px;
}

.filter input::placeholder {
    font-size: 16px;
    color: #495057;
}

@media(max-width:1250px) {
    .card {
        width: 80%
    }

    .characters {
        grid-template-columns: 1fr;
        place-items: center;
    }
}

@media(max-width:768px) {
    .card {
        width: 100%
    }

    .filter input {
        width: 50%;
    }
}